import React from "react";

const Main = ({ data, read }) => (
  <>
    <main id="about">
      <div class="container">
        <div class="row wow fadeIn" data-wow-delay="0.2s">
          <div class="col-lg-7">
            <div class="view overlay hm-white-light z-depth-1-half">
              <img src={read[0].image} class="img-fluid " alt="" />
              <div class="mask"></div>
            </div>
            <br />
          </div>

          <div class="col-lg-5">
            <h2 class="h2-responsive">{read[0].title}</h2>
            <hr />
            <p>{read[0].about}</p>
            {/* <a href="" class="btn btn-info">
              Contact
            </a> */}
          </div>
        </div>
        <hr class="extra-margins" id="portfolio" />
        <div class="row">
          <div class="col-lg-4">
            <div class="card wow fadeIn" data-wow-delay="0.4s">
              <div class="view overlay hm-white-slight">
                <img src={data[0].image} class="img-fluid" alt="" />
                <a href={data[0].href}>
                  <div class="mask"></div>
                </a>
              </div>

              <div class="card-block">
                <h4 class="card-title">{data[0].title}</h4>

                <p class="card-text">{data[0].about}<span className="hash">#웹표준</span></p>
                <a href={data[0].href} class="btn btn-info">
                  바로가기
                </a>
              </div>
            </div>
          </div>

          <div class="col-lg-4">
            <div class="card wow fadeIn" data-wow-delay="0.6s">
              <div class="view overlay hm-white-slight">
                <img src={data[1].image} class="img-fluid" alt="" />
                <a href={data[1].href}>
                  <div class="mask"></div>
                </a>
              </div>

              <div class="card-block">
                <h4 class="card-title">{data[1].title}</h4>

                <p class="card-text">{data[1].about}<span className="hash">#반응형</span></p>
                <a href={data[1].href} class="btn btn-info">
                  바로가기
                </a>
              </div>
            </div>
          </div>

          <div class="col-lg-4">
            <div class="card wow fadeIn" data-wow-delay="0.8s">
              <div class="view overlay hm-white-slight">
                <img src={data[2].image} class="img-fluid" alt="" />
                <a href={data[2].href}>
                  <div class="mask"></div>
                </a>
              </div>

              <div class="card-block">
                <h4 class="card-title">{data[2].title}</h4>

                <p class="card-text">{data[2].about}<span className="hash">#Javascript</span></p>
                <a href={data[2].href} class="btn btn-info">
                  바로가기
                </a>
              </div>
            </div>
          </div>
        </div>{" "}
        <div class="row">
          <div class="col-lg-4">
            <div class="card wow fadeIn" data-wow-delay="0.4s">
              <div class="view overlay hm-white-slight">
                <img src={data[3].image} class="img-fluid" alt="" />
                <a href={data[3].href}>
                  <div class="mask"></div>
                </a>
              </div>

              <div class="card-block">
                <h4 class="card-title">{data[3].title}</h4>

                <p class="card-text">{data[3].about}<span className="hash">#React</span>, <span className="hash">#Rest Api</span></p>
                <a href={data[3].href} class="btn btn-info">
                  바로가기
                </a>
                <a href="https://github.com/toppeak-web/youtube_clone" class="btn btn-info">
                  자세히보기
                </a>
              </div>
            </div>
          </div>

          <div class="col-lg-4">
            <div class="card wow fadeIn" data-wow-delay="0.6s">
              <div class="view overlay hm-white-slight">
                <img src={data[4].image} class="img-fluid" alt="" />
                <a href={data[4].href}>
                  <div class="mask"></div>
                </a>
              </div>

              <div class="card-block">
                <h4 class="card-title">{data[4].title}</h4>

                <p class="card-text">{data[4].about}<span className="hash">#Reactnative</span></p>
                <a href="https://expo.io/artifacts/9a412735-e40e-4dd3-bc96-ac83f2e720bd" class="btn btn-info">
                  다운로드
                </a>
                <a href="https://github.com/toppeak-web/fokin_weater" class="btn btn-info">
                  자세히 보기
                </a>
                
              </div>
            </div>
          </div>

          <div class="col-lg-4">
            <div class="card wow fadeIn" data-wow-delay="0.8s">
              <div class="view overlay hm-white-slight">
                <img src={data[5].image} class="img-fluid" alt="" />
                <a href={data[5].href}>
                  <div class="mask"></div>
                </a>
              </div>

              <div class="card-block">
                <h4 class="card-title">{data[5].title}</h4>

                <p class="card-text">{data[5].about}<span className="hash">#React</span><span className="hash">#Firebase</span></p>
                <a href={data[5].href} class="btn btn-info">
                  바로가기
                </a>
                <a href="https://github.com/toppeak-web/business-card-maker" class="btn btn-info">
                  깃허브 보기
                </a>
              </div>
            </div>
          </div>
        </div>
        </div>
    </main>
  </>
);

export default Main;
